.preview-page {
  width: 100%;
  height: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.preview-page h2 {
  color: rgb(70, 186, 171);
  font-size: 30px;
  font-weight: 500;
  margin-bottom: 40px;
  margin-top: 80px;
}

.preview-container {
  background: white;
  height: auto;
  width: 500px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-box-shadow: 2px 2px 0 2px rgba(22, 29, 37, 0.05);
          box-shadow: 2px 2px 0 2px rgba(22, 29, 37, 0.05);
  padding: 50px 0 50px 0;
  margin-bottom: 50px;
}

.add-guest-preview input {
  width: 250px;
  margin-right: 23px;
}

.link-container {
  margin-top: 30px;
  width: 100%;
  margin-right: 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.link-container button {
  margin-right: 10px;
}

@-webkit-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 0.8; }
}

@keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 0.8; }
}

.message {
  width: 100%;
  height: 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-color: rgb(70, 186, 171);
  opacity: 0.8;
  color: white;
  position: absolute;
  z-index: 100;
  -webkit-animation: fadein 1.5s;
          animation: fadein 1.5s;
}

.show {
  display: block;
}

.hide {
  display: none;
}

@media (max-width: 700px) {
  .link-container {
    -ms-flex-pack: center;
        justify-content: center;
    margin-right: 0;
  }
  .preview-container {
    width: 340px;
  }
  .add-guest-preview input {
    width: 170px;
  }
}



