.myevent-container {
  margin-top: 40px;
  width: 700px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  border-bottom: 1px solid #ccc;
  padding-bottom: 12px;
}

.guest-accordion {
  width: 350px;
  text-align: right;
}

.guest-accordion .add-btn {
  margin-left: 15px;
}

.myevent-container button {
  margin: 0;
}

.down {
  display: block;
}

.up {
  display: none;
}

#edit-guestList {
  margin-top: 20px;
}

@media (min-width: 700px) and (max-width: 900px) {
  .myevent-container {
    width: 600px;
  }
}

@media (max-width: 700px) {
  .myevent-container {
    width: 280px;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    margin-top: 20px;
    text-align: center;
    padding-bottom: 20px;
  }
  .event-info h4 {
    font-size: 14px;
  }
  .event-info p {
    font-size: 12px;
  }
  .guest-accordion {
    text-align: center;
    margin-top: 20px;
    width: 250px;
  }
  .guest-accordion .add-btn {
    margin-left: 8px;
  }
}



